import {
  Box,
  Button,
  Flex,
  FormControl,
  FormLabel,
  IconButton,
  Input,
  InputGroup,
  InputLeftElement,
  Link,
  Stack,
  Textarea,
  Tooltip,
  useClipboard,
  VStack,
  Text,
  FormErrorMessage,
  useToast,
} from '@chakra-ui/react';

import sendForm from "services/EmailSender";
import Title from "components/Title/Title";

const confetti = {
  light: {
    primary: 'F56565', // blue.400
    secondary: 'FED7D7', // blue.100
  },

  dark: {
    primary: '63171B', // blue.900
    secondary: '822727', // blue.800
  },
};

