const emailRegexPattern = /^(([^<>()[\]\\.,;:\s@]+(\.[^<>()[\]\\.,;:\s@]+)*)|(.+))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

function ContactForm() {
  const { t } = useTranslation();
  const toast = useToast();

  const form = useRef();

  const [isEmailSubmitting, setIsEmailSubmitting] = useState(false);
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm();

  /**
   * Sends and email with using the form data
   */
  function onSubmit() {
    if(isEmailSubmitting) {
      return;
    }

    setIsEmailSubmitting(true);
    sendForm(form)
      .then((isSentCorrectly) => {
        setIsEmailSubmitting(false);
        showToast(isSentCorrectly);
      });
  }

  /**
   * Shows a success or error toast when the
   * email is sent.
   * @param {boolean} isSuccess Whether the email 
   * has sent successfully or not.
   */
  function showToast(isSuccess) {
    let toastConfig = {
      duration: 5000,
      isClosable: true,
      colorScheme: 'red'
    }
    
    if(isSuccess) {
      toastConfig.status = 'success';
      toastConfig.title = t('email_sent_correctly_title');
      toastConfig.description = t('email_sent_correctly_message');
    }
    else {
      toastConfig.status = 'error';
      toastConfig.title = t('email_sent_error_title');
      toastConfig.description = t('email_sent_error_message');
    }

    toast(toastConfig);
  }

  return (
    <form ref={form} onSubmit={handleSubmit(onSubmit)}>
      <VStack spacing={5} color={'white'} opacity={1}>

        {/* NAME */}
        <FormControl isRequired isInvalid={errors.name}>
          <FormLabel htmlFor="name">{t('name')}</FormLabel>

          <InputGroup>
            <InputLeftElement children={<BsPerson />} />
            <Input 
              type="text" 
              name="name" 
              id="name" 
              placeholder={t('your_name')} 
              focusBorderColor={'red.400'}
              isRequired
              isReadOnly={isEmailSubmitting}
              {
                ...register('name', {
                  required: t('required'),
                  minLength: { value: 4, message: t('min_length') }
                })
              }
            />
          </InputGroup>

          <FormErrorMessage>
            {errors.name && errors.name.message}
          </FormErrorMessage>
        </FormControl>

        {/* EMAIL */}
        <FormControl isRequired isInvalid={errors.email}>
          <FormLabel htmlFor="email">{t('email')}</FormLabel>

          <InputGroup>
            <InputLeftElement children={<MdOutlineEmail />} />
            <Input
              type="email"
              name="email"
              id="email"
              placeholder={t('your_email')}
              focusBorderColor={'red.400'}
              isRequired
              isReadOnly={isEmailSubmitting}
              {
                ...register('email', {
                  required: t('required'),
                  minLength: { value: 4, message: t('min_length') },
                  pattern: { 
                    value: emailRegexPattern, 
                    message: t('email_error')
                  }
                })
              }
            />
          </InputGroup>

          <FormErrorMessage>
            {errors.email && errors.email.message}
          </FormErrorMessage>
        </FormControl>

        {/* MESSAGE */}
        <FormControl isRequired isInvalid={errors.message}>
          <FormLabel htmlFor="message">{t('message')}</FormLabel>

          <Textarea
            name="message"
            id="message"
            placeholder={t('your_message')}
            rows={6}
            resize="none"
            focusBorderColor={'red.400'}
            isRequired
            isReadOnly={isEmailSubmitting}
            {
              ...register('message', {
                required: t('required'),
                minLength: { value: 4, message: t('min_length') }
              })
            }
          />

          <FormErrorMessage>
            {errors.message && errors.message.message}
          </FormErrorMessage>
        </FormControl>

        <Button
          type="submit"
          value="Send"
          colorScheme="red"
          bg="red.400"
          color="white"
          _hover={{
            bg: 'red.500',
          }}
          isFullWidth
          isLoading={isEmailSubmitting}
        >
          {t('send_message')}
        </Button>
      </VStack>
    </form>
  )
}

function ContactSection() {
  const { t } = useTranslation();

  const { hasCopied, onCopy } = useClipboard('csanchezarisa@gmail.com');

  return (
    <Flex
      bg={'282c34'}
      align="center"
      justify="center"
      css={{
        backgroundImage: CONFETTI,
        backgroundAttachment: 'fixed',
      }}
      id="contact"
    >
      <Box
        borderRadius="lg"
        m={{ base: 2, md: 8, lg: 5 }}
        p={{ base: 2, lg: 5 }}>
        <Box>
          <VStack spacing={{ base: 4, md: 8, lg: 20 }}>
            <Title title={t('contact_me')} />
            <Text 
              color={'gray.300'}
              textAlign='center'
            >
              {t('contact_me_description')}
            </Text>
            <Stack
              spacing={{ base: 4, md: 8, lg: 20 }}
              direction={{ base: 'column', md: 'row' }}
            >
              <Stack
                align="center"
                justify="space-around"
                direction={{ base: 'row', md: 'column' }}
              >
                <Tooltip
                  label={hasCopied ? t('email_copied') : t('copy_email')}
                  closeOnClick={false}
                  hasArrow
                >
                  <IconButton
                    aria-label="email"
                    variant="ghost"
                    size="lg"
                    fontSize="3xl"
                    color={'whiteAlpha.800'}
                    colorScheme="red"
                    icon={<MdEmail />}
                    _hover={{
                      bg: 'red.400',
                      color: 'white',
                    }}
                    onClick={onCopy}
                    isRound
                  />
                </Tooltip>

                <Tooltip
                  label="GitHub"
                  hasArrow
                >
                  <Link href="https://github.com/csanchezarisa" target="_blank">
                    <IconButton
                      aria-label="github"
                      variant="ghost"
                      size="lg"
                      fontSize="3xl"
                      color={'whiteAlpha.800'}
                      colorScheme="red"
                      icon={<FaGithub />}
                      _hover={{
                        bg: 'red.400',
                        color: 'white',
                      }}
                      isRound
                    />
                  </Link>
                </Tooltip>
                
                <Tooltip
                  label="DockerHub"
                  hasArrow
                >
                  <Link href="https://hub.docker.com/u/csanchezarisa" target="_blank">
                    <IconButton
                      aria-label="dockerhub"
                      variant="ghost"
                      size="lg"
                      fontSize="3xl"
                      color={'whiteAlpha.800'}
                      colorScheme="red"
                      icon={<FaDocker />}
                      _hover={{
                        bg: 'red.400',
                        color: 'white',
                      }}
                      isRound
                    />
                  </Link>
                </Tooltip>

                <Tooltip
                  label="Instagram"
                  hasArrow
                >
                  <Link href="https://www.instagram.com/ulkiobal/" target="_blank">
                    <IconButton
                      aria-label="instagram"
                      variant="ghost"
                      size="lg"
                      color={'whiteAlpha.800'}
                      colorScheme="red"
                      icon={<FaInstagram size="28px" />}
                      _hover={{
                        bg: 'red.400',
                        color: 'white',
                      }}
                      isRound
                    />
                  </Link>
                </Tooltip>

                <Tooltip
                  label="LinkedIn"
                  hasArrow
                >
                  <Link href="https://www.linkedin.com/in/cristobal-sanchez-arisa/" target="_blank">
                    <IconButton
                      aria-label="linkedin"
                      variant="ghost"
                      size="lg"
                      color={'whiteAlpha.800'}
                      colorScheme="red"
                      icon={<FaLinkedin size="28px" />}
                      _hover={{
                        bg: 'red.400',
                        color: 'white',
                      }}
                      isRound
                    />
                  </Link>
                </Tooltip>
              </Stack>

              <Box
                bg={'#343a40'}
                borderRadius="lg"
                p={8}
                color={'gray.700'}
                shadow="base"
                opacity={0.8}
              >
                {ContactForm()}
              </Box>
            </Stack>
          </VStack>
        </Box>
      </Box>
    </Flex>
  );
}

export default ContactSection;
